  max-width: 1280px;
  padding: 0 20px;
  margin: 0 auto;
  position: relative;
}

@media (max-width: 575.98px) {
  .container {
    padding: 0 10px;
  }
}

@media (min-width: 768px) {
  .mob-visible {
    display: none !important;
  }
}

@media (max-width: 767px) {
