    right: -3.125rem;
    z-index: 4;
  }
  .header__nav ul.show {
    display: block;
  }
}

.header__nav ul li {
  margin: 0rem 1.5625rem;
}

@media (max-width: 1199px) {
  .header__nav ul li {
    margin: 0 0.9375rem;
  }
}

@media (max-width: 991px) {
  .header__nav ul li {
    margin: 0;
    text-align: right;
  }
}

.header__nav ul li a {
