}

.user-nav:hover .login-ico, .user-nav.active .login-ico {
  background: url("../assets/icons/login-purple.png") no-repeat 50%/contain;
}

.user-nav:hover .user-nav__menu, .user-nav.active .user-nav__menu {
  display: block;
}

.user-nav__menu {
  position: absolute;
  z-index: 5;
  top: 100%;
  width: 9.375rem;
  right: 0;
  background-color: #fff;
  display: none;
  padding: 0.3125rem 0;
}

.user-nav__menu li a {
  line-height: 2.1875rem;
