}

.menu-btn {
  display: none;
  width: 1.875rem;
  height: 1.25rem;
  transition: all .3s ease;
}

@media (max-width: 991px) {
  .menu-btn {
    display: block;
  }
}

.menu-btn.close-menu .line {
  position: relative;
}

.menu-btn.close-menu .line:nth-child(2) {
  opacity: 0;
}

.menu-btn.close-menu .line:nth-child(1) {
  transform: rotate(45deg);
  top: calc(50% - 1px);
}

.menu-btn.close-menu .line:nth-child(3) {
  transform: rotate(-45deg);
  bottom: calc(50% - 1px);
}

.menu-btn:hover {
  opacity: 0.85;
}

.menu-btn .lines {
  display: block;
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: column;
  justify-content: space-between;
}

.menu-btn .line {
  display: block;
  width: 100%;
  height: 0.125rem;
  background: #fff;
  transition: all .2s ease;
}

